    <transition name="slide-fade">
      <navMenu v-show="isShow"></navMenu>
    </transition>
    <transition name="slide-fade1">
      <div class="navArrow" v-show="arrowShow">
        <img src="@/assets/arrow.png" alt="" />
      </div>
    </transition>

    <div
      v-for="(item, index) in topMenu"
      :key="index"
      @click="changeMenu(item.name, index)"
      :class="{ isActive: index == activeIndex }"
      v-if="item.show"
    >
      <img :src="require('@/assets/menu/menu' + (index + 1) + '.png')" alt="" />
      {{ item.name }}
    </div>
    <div class="username">
      {{ userName }}
    </div>
    <div class="collect" v-show="true">
      <img
        @click="clickCollect"
        v-show="!collectIsCheck"
        src="@/assets/collecteNoCheck.png"
        style="width: 0.3rem; height: 0.3rem; cursor: pointer"
        alt=""
      />
      <img
        @click="clickCollect"
        v-show="collectIsCheck"
        src="@/assets/collecteCheck.png"
        style="width: 0.3rem; height: 0.3rem; cursor: pointer"
        alt=""
      />
    </div>
    <div class="exit">
      <img
        src="@/assets/exit.png"
        style="width: 0.2rem; height: 0.2rem"
