<script>
import { myCollection, loginout } from "@/network/home_view";
import navMenu from "../menu/navMenu.vue";
export default {
  //注入刷新依赖provide/inject
  inject: ["reload"],
  components: {
    navMenu,
  },
  data() {
    return {
      collectIsCheck: true,
      topMenu: [
        {
          name: "首页",
          show: true,
        },
        {
          name: "配电安全",
          show: false,
        },
        {
          name: "能源管理",
          show: false,
        },
        {
          name: "光伏管理",
          show: false,
        },
        {
          name: "节能减排",
          show: false,
        },
      ],
        ips:1,
        dropIps:[],
      activeIndex: 0,
      isShow: false,
      menuhideTimeout: null,
      currentViewName: "",
      dropArry: [],
      userName: "",
      currentArea: "",
      currentLogo: "",
      arrowShow: true,
    };
  },
  watch: {
    $route: {
      handler(to) {
        /**
         * 监听路由变化，判断当前页面有没有被收藏
         * **/
        this.currentViewName = to.meta.title;
        let viewArry = JSON.parse(sessionStorage.getItem("collectViews"));
        if (viewArry.includes(this.currentViewName)) {
          this.collectIsCheck = true;
        } else {
          this.collectIsCheck = false;
        }
      },
      deep: true, // 深度监听
      immediate: true, // 第一次初始化渲染就可以监听到
    },
  },
  created() {
    this.$bus.$on("showMenu", (val) => {
      this.showMenu();
      this.arrowShow = false;
    });
  },
  mounted() {
    // this.dropArry = JSON.parse(sessionStorage.getItem("companyArry"));
    // this.userName = sessionStorage.getItem("jh_userName");
    // this.currentArea = sessionStorage.getItem("companyName");
    // this.currentLogo = sessionStorage.getItem("companyLogo")
      this.function();

    let menuPermissions = JSON.parse(sessionStorage.getItem("menuPermissions"));
    this.topMenu = [
      {
        name: "首页",
        show: true,
      },
      {
        name: "配电安全",
        // show: menuPermissions.isPDF,
          show:true,
      },
      {
        name: "能源管理",
        // show: menuPermissions.isNY,
          show: true,
      },
      {
        name: "光伏管理",
        // show: menuPermissions.isGF,
          show: true,
      },
      {
        name: "节能减排",
        // show: menuPermissions.isHB,
          show: true,
      },
    ];

    if (sessionStorage.getItem("headerTabActiveIndex") != null) {
      this.activeIndex = sessionStorage.getItem("headerTabActiveIndex");
    }
  },
  methods: {
    clickCollect() {
      /**
       * 是否收藏客户端画面实现
       * **/
      this.collectIsCheck = !this.collectIsCheck;
      let collectName = this.$route.meta.title;
      let viewArry = JSON.parse(sessionStorage.getItem("collectViews")); //是否是藏的数组
      let type = 0;
      if (this.collectIsCheck) {
        viewArry.push(collectName);
        type = 0; //收藏
      } else {
        let newArry = viewArry.filter((item) => item != collectName);
        viewArry = newArry;
        type = 1; //取消收藏
      }
      sessionStorage.setItem("collectViews", JSON.stringify(viewArry));

      this.myCollectionFun(collectName, type);
    },
    /**
     * 是否收藏接口调用
     * **/
    // myCollectionFun(collectName, type) {
    //   myCollection(collectName, type)
    //     .then((res) => {
    //       const { content, errmsg, errcode } = res;
    //       if (errcode == 0) {
    //         let msg = "收藏成功！";
    //         if (type == 1) {
    //           msg = "取消收藏成功！";
    //         }
    //         this.$message({
    //           message: msg,
    //           type: "success",
    //         });
    //       } else {
    //         this.$message.error(errmsg);
    //       }
    //     })
    //     .catch((err) => {});
    // },
    /**
     * 退出登录
     * **/
    exitLoginFun() {
        sessionStorage.removeItem("headerTabActiveIndex"); //清除header 选中的index
        this.$message({
            message: "退出登录成功！",
            type: "success",
        });
        this.$router.push("/");

      // loginout()
      //   .then((res) => {
      //     const { content, errmsg, errcode } = res;
      //     if (errcode == 0) {
      //       sessionStorage.removeItem("headerTabActiveIndex"); //清除header 选中的index
      //       this.$message({
      //         message: "退出登录成功！",
      //         type: "success",
      //       });
      //       this.$router.push("/");
      //     } else {
      //       this.$message.error(errmsg);
      //     }
      //   })
      //   .catch((err) => {});
    },
    showMenu() {
      this.isShow = true;
      this.arrowShow = false;
      this.clearMenuhideTimeout();
    },
    hideMenu() {
      this.menuhideTimeout = setTimeout(() => {
        this.isShow = false;
        this.arrowShow = true;
      }, 100);
    },
    clearMenuhideTimeout() {
      clearTimeout(this.menuhideTimeout);
    },
    changeMenu(name, index) {
      this.$bus.$emit("bus_menu", name);
      this.activeIndex = index;
      sessionStorage.setItem("headerTabActiveIndex", index);
    },
    changCompany(item) {
        this.function()
      sessionStorage.setItem("companyId", item.id);
      sessionStorage.setItem("companyName", item.name);
      // sessionStorage.setItem("companyLogo", item.logo);

      this.reload();
    },
      function(){
        this.ips =Math.floor(Math.random()*2)+1
      }
  },
};
</script>
<style scoped lang="less">
.headerTitle {
  // width: 3.81rem;
  font-size: 0.22rem;
  letter-spacing: 0.02rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  padding: 0.18rem 0;
  padding-left: 0.31rem;
  cursor: pointer;
  box-sizing: border-box;
  padding-right: 0.3rem;
}
.header {
  background: #00001f;
  display: flex;
  color: #fff;
  height: 0.5rem;
}
.header > div {
  cursor: pointer;
  font-size: 0.12rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 1rem;
  letter-spacing: 0.01rem;
  > img {
    width: 0.16rem;
    height: 0.16rem;
    margin-bottom: 0.04rem;
  }
}
.isActive {
  background: #0655c6;
}

.slide-fade-enter-active {
  transition: all 0.5s ease;
}
.slide-fade-leave-active {
  transition: all 0.5s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(-100%);
  opacity: 0;
}

.slide-fade1-enter-active {
  transition: all 0.1s ease;
}
.slide-fade1-leave-active {
  transition: all 0.1s ease;
}
.slide-fade1-enter,
.slide-fade1-leave-to {
  transform: translateX(-100%);
  opacity: 0;
}

.collect {
  position: absolute;
  right: 0.7rem;
  top: 0.25rem;
  transform: translateY(-0.15rem);
  min-width: auto !important;
}
/deep/ .el-dropdown {
  min-width: unset !important;
  margin-right: 0.2rem;
}
.username {
  position: absolute;
  right: 1rem;
  top: 0.25rem;
  transform: translateY(-0.1rem);
}
.exit {
  position: absolute;
  min-width: auto !important;
  right: 0.2rem;
  top: 0.2rem;
  transform: translateY(-0.05rem);
}
.navArrow {
  position: absolute;
  left: 0;
  min-width: auto !important;
  top: 50%;
  transform: translateY(-50%);
  border-bottom: 1px solid rgba(34, 175, 237, 0.5);
  border-top: 1px solid rgba(34, 175, 237, 0.5);
  border-right: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.06rem;
  background: rgba(0, 55, 146, 0.2);
  padding: 0.08rem 0.01rem;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  opacity: 0.6;
  > img {
    width: 0.12rem !important;
    height: 0.12rem !important;
    margin-bottom: 0rem !important;
  }
}
</style>