import Vue from "vue"
import Vuex from "vuex"
 
Vue.use(Vuex);
 
export default new Vuex.Store({
    state:{
        cancelTokenArr: [] 
    },
    mutations:{
        PUSH_CANCEL(state, payload){
            state.cancelTokenArr.push(payload.cancelToken);
        },
        
        CLAER_CANCEL({cancelTokenArr}){
        
            cancelTokenArr.forEach(item => {
               item('路由跳转取消请求');
            });
        
            cancelTokenArr = [];
        }
    }
})