<template>
  <div id="app" @mousemove="moveEvent">
    <router-view v-if="isRouterAlive"/>
    <Loading v-show="loadingProps.spinning" />
  </div>
</template>
<script>
import Vue from "vue";
export default {
  data() {
    return {
      loadingProps: {
        spinning: false,
      },
      isRouterAlive:true
    };
  },
  provide() {
    return {
      reload: this.reload,
    };
  },
  beforeCreate() {
    /**将当前页面组件给到$app**/
    Vue.prototype.$app = this;
  },
  methods: {
    /**
     * 判断鼠标触碰到屏幕左边的边缘时，显示菜单
     * **/
    moveEvent(e) {
      if (e.clientX == 0) {
        this.$bus.$emit("showMenu", true);
      }
    },
    /**
     * 实现当前页面刷新
     * **/
    reload(){
      this.isRouterAlive = false
      this.$nextTick(function(){
        this.isRouterAlive = true
      })
    }
  },
};
</script>
