<template>
  <div
    class="menu"
    :style="{ width: menuWidth, height: '100%' }"
    @mouseover="showMenu()"
    @mouseleave="hideMenu()"
  >
    <el-menu
      :unique-opened="true"
      :default-active="activeIndex"
