      background-color="rgba(0, 0, 0,0.6)"
      text-color="#fff"
      active-text-color="#fff"
      @open="handleOpen"
      @close="handleClose"
      :collapse="isCollapse"
      :router="true"
      @select="menuSelect"
    >
      <div v-for="(item, index) in elMenuArry" :key="index">
        <el-submenu :index="item.path" v-if="item.child">
          <template slot="title">
            <img
              style="width: 0.25rem; height: 0.25rem; margin-right: 0.29rem"
              :src="require('@/assets/menu/' + item.icon + '.png')"
              alt=""
            />
            <span slot="title" style="font-size: 0.2rem">{{ item.name }}</span>
          </template>
          <!-- 区分是否是外部链接 -->
          <el-menu-item
            :index="line.path"
            v-for="(line, i) in item.child"
            :key="i"
            v-if="line.type == 0"
          >
            <span
              style="
                border-radius: 50%;
                display: inline-block;
                width: 0.07rem;
                height: 0.07rem;
                background: #7d7d89;
                margin-right: 0.36rem;
                margin-left: 0.1rem;
              "
            ></span>
            <span style="font-size: 0.18rem">{{ line.name }}</span>
          </el-menu-item>
          <el-menu-item
            index=""
            v-for="(line, i) in item.child"
            :key="i"
            v-if="line.type == 1"
          >
