import axios from "axios";
import Vue from 'vue'
import router from '../router'
import messageup from '../utils/resetMessage'
axios.defaults.withCredentials = true
import global from '../utils/global'

// 当前正在请求的数量
let needLoadingRequestCount = 0
export function request(config) {
    /**
     * 判断是不是要显示loading，根据需求添加loading
     * 现在接口加载有两种loading（1.全屏loading 2.div容器loading）
     * 在这里控制全屏loading
     * **/
    let showLoad = false
    if (config.headers) {
        showLoad = config.headers.showLoading
    }
    // 创建axios实例
    const intance = axios.create({
        // baseURL: '/web',
        // timeout: 30000,
        headers: { "Content-Type": "application/x-www-form-urlencoded" }
    })
    // 请求拦截的作用
    intance.interceptors.request.use(config => {

        config.cancelToken = global.store.source.token;

        if (showLoad) {
            needLoadingRequestCount++;
            Vue.prototype.$setLoading(true)
        }
        // 暂时不做任何的处理
        return config
    }, err => {
        // console.log(err);
    })

    // 响应拦截
    intance.interceptors.response.use(res => {
        console.log(res.data)
        //如果接口正常返回下 正常执行
        if (res.data.errcode == 0) {
            if (showLoad) {
                hideLoding()
            }
            return res.data
        } else {
            hideLoding()
            checkLoginFun().then(data=>{
                if(data.data.errcode == 0){
                    return res.data
                }else{
                    messageup({
                        type:'error',
                        showClose:false,
                        message:data.data.errmsg,
                    })
                    router.push('/')
                }
            }).catch(err=>{

            })
        }
    }, err => {
        if (showLoad) {
            hideLoding()
        }
        // console.log(err);
    })
    // 发送真正的网络请求
    return intance(config)
}
function hideLoding() {
    needLoadingRequestCount--;
    if (needLoadingRequestCount == 0) {
        Vue.prototype.$setLoading(false)
    }
}
