import Vue from 'vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue'
import router from './router'
import bus from '@/utils/bus'
import isNumber from '@/utils/isNumber'
import getMonthDay from '@/utils/getMonthDay'
import getDate from '@/utils/getDate'
import arrToTree from '@/utils/arrToTree'
import isInteger from '@/utils/isInteger'
import store from './store'

import axios  from 'axios';
import global from '@/utils/global'

import * as echarts from 'echarts'

import '@/assets/css/public.less'

import $ from 'jquery'
window.$ = $

import Loading from "./components/Loading/index"
Vue.use(Loading);

Vue.prototype.$echarts = echarts;

Vue.use(ElementUI);

Vue.prototype.$bus = bus
Vue.prototype.$isNumber = isNumber;//判断是不是数字类型
Vue.prototype.$getMonthDay = getMonthDay;//获取这个月有几天
Vue.prototype.$getDate = getDate;//获取当前年月日
Vue.prototype.$arrToTree = arrToTree;//扁平数组转树形结构数组
Vue.prototype.$isInteger = isInteger;//判断是不是整数

// Vue.prototype.$companyId = 'A2109160001';//区域编号
// Vue.prototype.$ipAddress = 'yff.jingheqiye.com:50508';
Vue.prototype.$ipAddress = window.location.host;

Vue.config.productionTip = false

import dataV from '@jiaminghi/data-view'

Vue.use(dataV)

//页面根据分辨率自适应
window.onresize = setHtmlFontSize
function setHtmlFontSize() {
  const htmlWidth = document.documentElement.clientWidth || document.body.clientWidth
  const htmlDom = document.getElementsByTagName('html')[0]
  htmlDom.style.fontSize = htmlWidth / 19.2 + 'px'//根据设计稿转换单位1rem = 100px
}
setHtmlFontSize()

Number.prototype.toFixed = function (s) {
	var that = this, changenum, index;
	if (this < 0) {
		that = -that;
	}
	changenum = (parseInt(that * Math.pow(10, s) + 0.5) / Math.pow(10, s)).toString();
	index = changenum.indexOf(".");
	if (index < 0 && s > 0) {
		changenum = changenum + ".";
		for (var i = 0; i < s; i++) {
			changenum = changenum + "0";
		}
	} else {
		index = changenum.length - index;
		for (var i = 0; i < (s - index) + 1; i++) {
			changenum = changenum + "0";
		}
 
 
	}
	if (this < 0) {
		return -changenum;
	} else {
		return changenum;
	}
}


Vue.prototype.$setLoading = function (props) {
  if (typeof props === 'boolean') props = { spinning: props }
  if (Object.prototype.toString.call(props) !== '[object Object]') props = {}
  this.$app.loadingProps = {
    ...props
  }
}
router.beforeEach((to, from, next) => {
  const CancelToken = axios.CancelToken
  global.store.source.cancel && global.store.source.cancel()
  global.store.source = CancelToken.source()
  next()
})
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
